import * as Yup from 'yup';
import isEqual from 'lodash/isEqual';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { useMemo, useState, useEffect, useCallback } from 'react';

import { LoadingButton } from '@mui/lab';
import Select from '@mui/material/Select';
import Divider from '@mui/material/Divider';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import OutlinedInput from '@mui/material/OutlinedInput';
import { TimePicker, renderTimeViewClock } from '@mui/x-date-pickers';
import { Box, Card, Stack, Button, MenuItem, IconButton } from '@mui/material';

import { paths } from 'src/routes/paths';
import { RouterLink } from 'src/routes/components';
import { useRouter, useSearchParams } from 'src/routes/hooks';

import { useBoolean } from 'src/hooks/use-boolean';
import { useDebounce } from 'src/hooks/use-debounce';

import { addToCalendar } from 'src/utils/calender';
import { useUnitTime } from 'src/utils/format-time';
import axiosInstance, { endpoints } from 'src/utils/axios';

import { useAuthContext } from 'src/auth/hooks';
import { useLocales, useTranslate } from 'src/locales';
import { useAclGuard } from 'src/auth/guard/acl-guard';
import {
  useFindPatient,
  useGetCountries,
  useFindUSPatient,
  useGetAppointment,
  useGetEmployeeAppointments,
  useGetEmployeeActiveWorkGroups,
} from 'src/api';

import Iconify from 'src/components/iconify';
import { useSnackbar } from 'src/components/snackbar';
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
import FormProvider from 'src/components/hook-form/form-provider';
import { RHFSelect, RHFTextField, RHFRadioGroup, RHFPhoneNumber } from 'src/components/hook-form';

import BookDetails from '../book-details';
import PatientsFound from '../patients-found';
import AddEmegencyAppointment from '../add-emergency-appointment';

// ----------------------------------------------------------------------
const defaultFilters = {
  status: 'available',
  work_group: null,
  startTime: null,
  endTime: null,
};
export default function TableCreateView() {
  const { user } = useAuthContext();
  const router = useRouter();

  const { t } = useTranslate();
  const { currentLang } = useLocales();
  const curLangAr = currentLang.value === 'ar';
  const { countriesData } = useGetCountries({ select: 'name_english name_arabic' });

  const { myunitTime } = useUnitTime();
  const addModal = useBoolean();
  const toturial = useBoolean(true);
  const checkAcl = useAclGuard();

  const [filters, setFilters] = useState(defaultFilters);
  const [page, setPage] = useState(1);

  const canReset = !isEqual(filters, defaultFilters);

  const searchParams = useSearchParams();
  const appointment = searchParams.get('appointment');
  const day = searchParams.get('day');

  const [selectedDate, setSelectedDate] = useState(day ? new Date(day) : new Date());

  const { appointmentsData, AppointDates, loading, refetch } = useGetEmployeeAppointments(
    user?.employee?.employee_engagements?.[user?.employee.selected_engagement]?._id,
    {
      ...filters,
      startDate: selectedDate,
      select: 'start_time',
    }
  );

  useEffect(() => {
    if (!loading && !appointmentsData.length && AppointDates.length) {
      setSelectedDate(AppointDates[0]);
    }
  }, [loading, appointmentsData.length, AppointDates]);

  const [selected, setSelected] = useState(appointment);

  const { workGroupsData } = useGetEmployeeActiveWorkGroups(
    user?.employee?.employee_engagements?.[user?.employee.selected_engagement]?._id
  );

  const { data: selectedAppointData } = useGetAppointment(selected, { select: 'work_group' });

  const { enqueueSnackbar } = useSnackbar();

  const NewUserSchema = Yup.object().shape({
    sequence_number: Yup.string(),
    name_english: Yup.string().test(
      'name-required',
      t('Either English or Arabic name is required'),
      // eslint-disable-next-line
      function (value) {
        // eslint-disable-next-line
        return value || this.parent.name_arabic;
      }
    ),
    name_arabic: Yup.string(),
    nationality: Yup.string().required(t('required field')),
    mobile_num1: Yup.string().required(t('required field')),
    mobile_num2: Yup.string(),
    note: Yup.string(),
    patientExist: Yup.string(),
  });

  const defaultValues = useMemo(
    () => ({
      sequence_number: '',
      name_english: '',
      name_arabic: '',
      nationality: null,
      mobile_num1: '',
      mobile_num2: '',
      note: '',
      patientExist: 'my_patients',
      work_group: workGroupsData?.[0]?._id,
      unit_service:
        user?.employee?.employee_engagements?.[user?.employee.selected_engagement]?.unit_service
          ?._id,
    }),
    [workGroupsData, user?.employee]
  );

  const methods = useForm({
    mode: 'all',
    resolver: yupResolver(NewUserSchema),
    defaultValues,
  });
  const {
    reset,
    handleSubmit,
    watch,
    setValue,
    formState: { isSubmitting, errors },
  } = methods;

  useEffect(() => {
    const keys = Object.keys(errors);
    if (keys.length) enqueueSnackbar(errors?.[keys[0]]?.message || 'error', { variant: 'error' });
  }, [errors, enqueueSnackbar]);

  const values = watch();

  const debouncedQuery = useDebounce({
    sequence_number: values.sequence_number,
    name_english: values.name_english,
    name_arabic: values.name_arabic,
    mobile_num1: values.mobile_num1,
    mobile_num2: values.mobile_num2,
  });

  const { existPatients } = useFindPatient({
    ...debouncedQuery,
    select:
      'code name_english name_arabic mobile_num1 mobile_num2 identification_num sequence_number nationality birth_date',
  });

  const { foundPatients } = useFindUSPatient({
    ...debouncedQuery,
    work_group: selectedAppointData?.work_group,
    select:
      'code name_english name_arabic mobile_num1 mobile_num2 identification_num sequence_number nationality patient birth_date',
  });

  const onSubmit = handleSubmit(async (data) => {
    try {
      delete data.sequence_number;
      await axiosInstance.patch(
        endpoints.appointments.patient.createPatientAndBookAppoint(selected),
        { ...data, lang: curLangAr }
      );
      const SelectedAppointment = appointmentsData.find((appoint) => appoint._id === selected);
      await addToCalendar(SelectedAppointment);
      enqueueSnackbar(t('created successfully!'));
      reset();
      router.back();
    } catch (error) {
      enqueueSnackbar(
        curLangAr ? `${error.arabic_message}` || `${error.message}` : `${error.message}`,
        {
          variant: 'error',
        }
      );
      refetch();
      console.error(error);
    }
  });

  const handleArabicInputChange = (event) => {
    // Validate the input based on Arabic language rules
    const arabicRegex = /^[\u0600-\u06FF0-9\s!@#$%^&*_\-().]*$/; // Range for Arabic characters

    if (arabicRegex.test(event.target.value)) {
      setValue(event.target.name, event.target.value);
    }
  };

  const handleEnglishInputChange = (event) => {
    // Validate the input based on English language rules
    const englishRegex = /^[a-zA-Z0-9\s,@#$!*_\-&^%.()]*$/; // Only allow letters and spaces

    if (englishRegex.test(event.target.value)) {
      setValue(event.target.name, event.target.value);
    }
  };

  const sequenceChangeHandler = (event) => {
    const { value } = event.target;
    const newValue = value.replace(/\D/g, '');
    if (newValue.length <= 3) {
      setValue(event.target.name, newValue);
    } else if (newValue.length >= 4 && newValue.charAt(3) === '-') {
      setValue(event.target.name, newValue.slice(0, 3));
    } else if (newValue.length >= 4 && newValue.charAt(3) !== '-') {
      setValue(event.target.name, `${newValue.slice(0, 3)}-${newValue.slice(3)}`);
    } else {
      setValue(event.target.name, newValue);
    }
  };

  const handleResetFilters = useCallback(() => {
    setFilters(defaultFilters);
  }, []);

  return (
    <>
      <Container maxWidth="xl">
        <CustomBreadcrumbs
          heading={t('Book Appointment')}
          links={[
            {
              name: t('dashboard'),
              href: paths.employee.root,
            },
            {
              name: t('appointments'),
              href: paths.employee.appointments.root,
            },
            { name: t('book') },
          ]}
          action={
            checkAcl({ category: 'work_group', subcategory: 'appointments', acl: 'create' }) && (
              <Button
                component={RouterLink}
                onClick={() => addModal.onTrue()}
                variant="contained"
                startIcon={<Iconify icon="mingcute:add-line" />}
                sx={{
                  bgcolor: 'error.dark',
                  '&:hover': {
                    bgcolor: 'error.main',
                  },
                }}
              >
                {t('new urgent appointment')}
              </Button>
            )
          }
          sx={{
            mb: { xs: 3, md: 5 },
          }}
        />
        <FormProvider methods={methods} onSubmit={onSubmit}>
          <Card sx={{ px: 3, pb: 3, pt: 2 }}>
            {page === 1 && (
              <>
                <Typography variant="h6">{t('Appointment details')}:</Typography>
                <Divider sx={{ borderStyle: 'dashed', my: 1 }} />
                <Box
                  sx={{ mb: 3, mx: 3, mt: 3 }}
                  rowGap={3}
                  columnGap={2}
                  display="flex"
                  alignItems="center"
                >
                  <Typography variant="subtitle2">{t('filters')}:</Typography>
                  <FormControl
                    sx={{
                      width: { xs: 1, md: 200 },
                    }}
                  >
                    <InputLabel shrink>{`${t('work group')}`}</InputLabel>

                    <Select
                      value={filters.group}
                      onChange={(event) =>
                        setFilters((prev) => ({ ...prev, group: event.target.value }))
                      }
                      size="small"
                      input={<OutlinedInput label={t('work group')} />}
                      // renderValue={(selected) =>
                      //   selected
                      // }
                      // MenuProps={{
                      //   PaperProps: {
                      //     sx: { maxHeight: 240 },
                      //   },
                      // }}
                    >
                      {workGroupsData.map((option, idx) => (
                        <MenuItem lang="ar" key={idx} value={option._id}>
                          {curLangAr ? option?.name_arabic : option?.name_english}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                  <TimePicker
                    viewRenderers={{
                      hours: renderTimeViewClock,
                      minutes: renderTimeViewClock,
                      seconds: renderTimeViewClock,
                    }}
                    ampm={false}
                    format="hh:mm a"
                    closeOnSelect
                    slots={{
                      actionBar: 'cancel',
                    }}
                    minutesStep={5}
                    label={t('from time')}
                    value={myunitTime(filters.startTime)}
                    onChange={(newValue) => {
                      const selectedTime = myunitTime(newValue);
                      setFilters((prev) => ({ ...prev, startTime: selectedTime }));
                    }}
                    slotProps={{ textField: { fullWidth: true, size: 'small' } }}
                    sx={{
                      width: { xs: 1, md: 200 },
                    }}
                  />

                  <TimePicker
                    viewRenderers={{
                      hours: renderTimeViewClock,
                      minutes: renderTimeViewClock,
                      seconds: renderTimeViewClock,
                    }}
                    ampm={false}
                    format="hh:mm a"
                    closeOnSelect
                    slots={{
                      actionBar: 'cancel',
                    }}
                    minutesStep={5}
                    label={t('to time')}
                    value={myunitTime(filters.endTime)}
                    onChange={(newValue) => {
                      const selectedTime = myunitTime(newValue);
                      setFilters((prev) => ({ ...prev, endTime: selectedTime }));
                    }}
                    slotProps={{
                      textField: {
                        fullWidth: true,
                        size: 'small',
                      },
                    }}
                    sx={{
                      width: { xs: 1, md: 200 },
                    }}
                  />

                  {canReset && (
                    <IconButton sx={{ color: 'error.main' }} onClick={handleResetFilters}>
                      <Iconify icon="solar:trash-bin-trash-bold" />
                    </IconButton>
                  )}
                </Box>

                <Divider sx={{ borderStyle: 'dashed' }} />
                {!loading && (
                  <BookDetails
                    selected={selected}
                    AppointDates={AppointDates}
                    loading={loading}
                    setSelected={setSelected}
                    selectedDate={selectedDate}
                    setSelectedDate={setSelectedDate}
                    list={appointmentsData}
                    // sx={{ mt: SPACING }}
                  />
                )}
              </>
            )}
            {page === 2 && (
              <>
                <Typography variant="h6">{t('Patient information')}:</Typography>
                <Divider sx={{ borderStyle: 'dashed', my: 1 }} />
                {!loading && (
                  <BookDetails
                    selected={selected}
                    AppointDates={AppointDates}
                    loading={loading}
                    setSelected={setSelected}
                    selectedDate={selectedDate}
                    setSelectedDate={setSelectedDate}
                    list={appointmentsData}
                    hideSelect
                  />
                )}
                {selected && (
                  <>
                    <Typography
                      sx={{ py: 2, fontWeight: 700 }}
                      variant="caption"
                      color="text.secondary"
                      textTransform="uppercase"
                    >
                      {t('patient information')}
                    </Typography>
                    <Stack direction="row" mt={2} gap={5}>
                      <RHFRadioGroup
                        row
                        name="patientExist"
                        options={[
                          { label: t('my patient'), value: 'my_patients' },
                          // { label: t('hakeemna patient'), value: 'exist' },
                          { label: t('new patient'), value: 'new' },
                        ]}
                      />
                      <RHFTextField
                        InputLabelProps={{ shrink: true }}
                        inputProps={{ style: { textAlign: 'center' } }}
                        sx={{ width: '200px', mb: 3, textAlign: 'center' }}
                        size="small"
                        name="sequence_number"
                        onChange={sequenceChangeHandler}
                        placeholder="001-22"
                        label={t('patient-code')}
                      />
                    </Stack>
                    {values.patientExist === 'my_patients' && (
                      <Box
                        sx={{ mb: 3 }}
                        rowGap={3}
                        columnGap={2}
                        display="grid"
                        gridTemplateColumns={{
                          xs: 'repeat(1, 1fr)',
                          sm: 'repeat(3, 1fr)',
                        }}
                      >
                        <RHFTextField
                          onChange={handleEnglishInputChange}
                          name="name_english"
                          label={t('patient name in english')}
                        />
                        <RHFTextField
                          onChange={handleArabicInputChange}
                          name="name_arabic"
                          label={t('patient name in arabic')}
                        />
                        <RHFTextField type="email" name="email" label={t('email address')} />
                        <RHFTextField
                          onChange={handleEnglishInputChange}
                          name="identification_num"
                          label={t('ID number')}
                        />
                        <RHFPhoneNumber name="mobile_num1" label={t('mobile number')} />
                        <RHFPhoneNumber name="mobile_num2" label={t('alternative mobile number')} />
                      </Box>
                    )}
                    {values.patientExist === 'exist' && (
                      <Box
                        sx={{ mb: 3 }}
                        rowGap={3}
                        columnGap={2}
                        display="grid"
                        gridTemplateColumns={{
                          xs: 'repeat(1, 1fr)',
                          sm: 'repeat(3, 1fr)',
                        }}
                      >
                        <RHFTextField
                          onChange={handleEnglishInputChange}
                          name="name_english"
                          label={t('patient name in english')}
                        />
                        <RHFTextField
                          onChange={handleArabicInputChange}
                          name="name_arabic"
                          label={t('patient name in arabic')}
                        />
                        <RHFTextField type="email" name="email" label={t('email address')} />
                        <RHFTextField
                          onChange={handleEnglishInputChange}
                          name="identification_num"
                          label={t('ID number')}
                        />
                        <RHFPhoneNumber name="mobile_num1" label={t('mobile number')} />
                        <RHFPhoneNumber name="mobile_num2" label={t('alternative mobile number')} />
                      </Box>
                    )}
                    {values.patientExist === 'new' && (
                      <>
                        <Box
                          sx={{ mb: 3 }}
                          rowGap={3}
                          columnGap={2}
                          display="grid"
                          gridTemplateColumns={{
                            xs: 'repeat(1, 1fr)',
                            sm: 'repeat(3, 1fr)',
                          }}
                        >
                          <RHFTextField
                            onChange={handleEnglishInputChange}
                            name="name_english"
                            label={t('patient name in english')}
                          />
                          <RHFTextField
                            onChange={handleArabicInputChange}
                            name="name_arabic"
                            label={t('patient name in arabic')}
                          />
                          <RHFPhoneNumber name="mobile_num1" label={t('mobile number')} />
                          <RHFPhoneNumber
                            name="mobile_num2"
                            label={t('alternative mobile number')}
                          />
                          <RHFSelect name="nationality" label={t('nationality')}>
                            {countriesData.map((option, idx) => (
                              <MenuItem lang="ar" key={idx} value={option._id}>
                                {curLangAr ? option?.name_arabic : option?.name_english}
                              </MenuItem>
                            ))}
                          </RHFSelect>
                        </Box>
                        {/* <RHFTextField multiline rows={2} name="note" label={t('note')} /> */}
                      </>
                    )}
                    {existPatients.length > 0 && values.patientExist === 'new' && (
                      <PatientsFound
                        SelectedAppointment={appointmentsData.find(
                          (appoint) => appoint._id === selected
                        )}
                        selected={selected}
                        reset={reset}
                        oldPatients={existPatients}
                      />
                    )}
                    {foundPatients.length > 0 && values.patientExist === 'my_patients' && (
                      <PatientsFound
                        SelectedAppointment={appointmentsData.find(
                          (appoint) => appoint._id === selected
                        )}
                        selected={selected}
                        reset={reset}
                        oldPatients={foundPatients}
                        usPatients
                      />
                    )}
                  </>
                )}
              </>
            )}
            <Stack direction="row" gap={2} justifyContent="flex-end" sx={{ mt: 3 }}>
              {page === 2 && (
                <Button variant="contained" color="warning" onClick={() => setPage(1)}>
                  {t('Back')}
                </Button>
              )}
              {page === 1 && (
                <Button
                  disabled={!selected}
                  variant="contained"
                  color="secondary"
                  onClick={() => setPage(2)}
                >
                  {t('Next')}
                </Button>
              )}
              {page === 2 && values.patientExist === 'new' && (
                <LoadingButton
                  disabled={!selected}
                  type="submit"
                  tabIndex={-1}
                  variant="contained"
                  loading={isSubmitting}
                >
                  {t('create new patient and book')}
                </LoadingButton>
              )}
            </Stack>
            {toturial.value && page === 2 && values.patientExist === 'new' && (
              <Box
                sx={{
                  position: 'absolute',
                  top: 0,
                  right: 0,
                  backgroundColor: 'primary.lighter',
                  p: 2,
                  pt: 1,
                  maxWidth: 350,
                  zIndex: 50,
                  borderRadius: 2,
                }}
              >
                <Stack direction="row" justifyContent="flex-end">
                  <IconButton onClick={toturial.onFalse}>
                    <Iconify icon="mingcute:close-line" />
                  </IconButton>
                </Stack>
                <Typography variant="subtitle2">
                  {t('If the patient is new to the clinic, please note the following:')}
                </Typography>
                <br />
                <Typography variant="body2">{t('Firstly')}:</Typography>
                <Typography variant="caption">
                  {t('If the patient has an active account on the Hakeemna 360 platform')}:
                </Typography>
                <br />
                <li style={{ fontSize: 12 }}>
                  {t(
                    'Please enter the patient identification code on the platform to facilitate the identification process; or:'
                  )}
                </li>
                <li style={{ fontSize: 12 }}>
                  {t('Enter some patient information such as name or phone number.')}
                </li>
                <br />
                <Typography variant="body2">{t('Secondly')}:</Typography>
                <Typography variant="caption">
                  {t(
                    'If the patient does not have an active account on the Hakeemna platform, in this case a new account must be created and the following data must be stored:'
                  )}
                  :
                </Typography>
                <br />
                <li style={{ fontSize: 12 }}>
                  {t(
                    'Name in Arabic, nationality and phone number and click on the button “Create a patient account and book an appointment”.'
                  )}
                </li>
              </Box>
            )}
          </Card>
        </FormProvider>
      </Container>
      <AddEmegencyAppointment refetch={refetch} open={addModal.value} onClose={addModal.onFalse} />
    </>
  );
}
